@import url("https://fonts.googleapis.com/css2?family=JetBrains+Mono&display=swap");
@import url("https://cdn-uicons.flaticon.com/uicons-regular-straight/css/uicons-regular-straight.css");

* {
    padding: 0;
    margin: 0;
    font-family: "JetBrains Mono", monospace;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
    -webkit-appearance: none;
}

html, body
{
    height: 100%;
    min-height: 100%;
}

.center-items {
    display: flex;
    justify-content: center;
    align-items: center;
}

.center-items-inline {
    display: flex;
    align-items: center;
}

.flex-row {
    display: flex;
    flex-direction: row;
    align-items: center;
}

.flex-column {
    display: flex;
    flex-direction: column;
    align-items: center;
}
.sp_btw {
    width: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
}

.w100 {
    width: 100%;
}

.custom-scrollbar {
    &::-webkit-scrollbar {
        width: 5px;
        height: 10px;
        margin-left: 10px;
    }

    &::-webkit-scrollbar-thumb {
        display: none;
    }
}

.shadow {
    -webkit-box-shadow: 0px 7px 10px 4px rgba(0,0,0,0.3);
    -moz-box-shadow: 0px 7px 10px 4px rgba(0,0,0,0.3);
    box-shadow: 0px 7px 10px 4px rgba(0,0,0,0.3);
}

.flex-space-center-wrap {
    display: flex;
    flex-direction: row;
    justify-content: center;
    flex-wrap: wrap;
}

.padding-content {
    padding: 1vh 2vw;
    box-sizing: border-box;
}

.border-2px {
    border: 2px solid #000;
}

.btn {
    text-align: center;
    //padding: 0.3vh 1vw;
    padding: 1vh 0.5vh;
    box-sizing: border-box;
    width: 100%;
    cursor: pointer;
    background: rgba(0, 0, 0, .2);
    font-size: 1.5vh;
    transition: all 0.3s ease;

    &:not(.disabled):hover {
        //animation: rotation 0.3s linear;
        //background: rgba(0, 0, 0, .4);
        transform: scaleY(1.1);
    }

    &.disabled {
        background: rgba(0, 0, 0, .2);
    }
    
    &.apply {
        background: rgba(31, 233, 28, 0.8);
    }

    &.auth {
        width: 30vw;
        background:rgba(245, 191, 66, .7);
    }
}

.padding-lr {
    padding: 1vh 2vh;
    box-sizing: border-box;
}

.hide {
    display: none;
}

// breakpoints
@media (max-width:1200px) {
    .qItem, .QmakerContainer {
        min-width: 20vw;
    }
}
@media (max-width:840px) {
    .qItem, .QmakerContainer {
        min-width: 30vw;
    }
    .input_group {
        min-width: 75vw;
    }
}
@media (max-width:700px) {
    .title {
        display: none;
    }
    .profile {
        width: 100%;
    }
    .qItem, .QmakerContainer {
        min-width: 45vw;
    }
    li.item {
        overflow: hidden;
        .position {
            min-width: 5%;
        }
        .nameStudent {
            min-width: 60%;
        }
        .btn_container {
            max-width: 30%;
            height: 100%;
        }
    }
}
@media (max-width: 400px) {
    .qList-item {
        height: 5vh;
        > * {
            font-size: 0.8em;
        }
    }
    .notice {
        min-width: 90vw;
    }
    .dev {
        font-size: 0.8em;
    }
}
// breakpoints end
@keyframes countdown {
    from {
        width: 100%;
    }
    to {
        width: 0;
    }
}

// colors
$mainColor : #494748;
$headerColor : #E6195E;
$contentColor : #EDFCF1;
$white : #ffffff;

body {
    min-height: 100vh;
    background: $mainColor;

    footer {
        position: fixed;
        padding: 0 3vw;
        box-sizing: border-box;
        height: 5vh;
        width: 100%;
        bottom: 0;
        background-color: $headerColor;
        color: white;

        .dev {
            height: 100%;
            > * {
                margin-right: 1vw;
            }

            .link {
                height: 100%;
                transition: all 0.2s;
                cursor: pointer;
            }

            .link:hover {
                background-color: rgba(255, 255, 255, 0.4);
            }
        }
    }

    .notification {
        position: absolute;
        bottom: 8vh;
        width: 100%;
        animation: fold 0.5s;
        z-index: 1000;

        .notification-content {
            color: white;
            position: relative;
            .countdown {
                position: absolute;
                bottom: 5%;
                left: 0;
                width: 100%;
                height: 2px;
                background: #fff;
                animation: countdown 5s ease-in-out;
            }
        }
    }

    .load {
        position: fixed;
        height: 100%;
        width: 100%;
        background-color: rgba(0, 0, 0, .4);

        &::after {
            position: absolute;
            content: "Подождите";
            color: #fff;
            font-weight: 800;
        }

        .square {
            height: 15vh;
            width: 15vh;
            animation: loading 2s ease-in-out infinite;
        }
    }

    .modal {
        position: fixed;
        width: 100%;
        height: 100%;
        animation: fold 0.5s;
        animation-timing-function: ease;
        background-color: rgba(0, 0, 0, .4);
        transition: opacity 0.5s;

        &:focus {
            outline: none;
        }

        &.visible {
            opacity: 1;
        }

        .form {
            flex-direction: column;
            background-color: $white;

            > * {
                margin-top: 1vh;
            }

            .input_group {
                position: relative;
                width: 30vw;

                input {
                    width: 100%;
                    height: auto;
                    padding: 1vh 0.5vh;
                    box-sizing: border-box;
                    border: 1px solid #000;
                    background-color: $white;
                }

                i {
                    width: 3vh;
                    height: 3vh;
                    position: absolute;
                    right: 0;
                    top: 1.2vh;
                    opacity: 0;
                    transition: all 0.2s;
                }

                &.success {
                    input {
                        border-color:rgba(0, 240, 0, 0.5);
                    }
                    i.fi-rs-check {
                        opacity: 1;
                        color:rgba(0, 240, 0, 0.9);
                    }
                }

                &.error {
                    input {
                        border-color:rgba(240, 0, 0, 0.9);
                    }
                    i.fi-rs-exclamation {
                        opacity: 1;
                        color:rgba(240, 0, 0, 0.9);
                    }
                    &.message .error_message {
                        height: 100%;
                    }
                    &.type_err .error_type {
                        height: 100%;
                    }
                }

                .error_message,
                .error_type {
                    font-size: 0.8em;
                    color:rgba(240, 0, 0, 0.9);
                    overflow: hidden;
                    height: 0;
                }

                input:focus + .field_name, input:valid + .field_name {
                    font-size: .8em;
                    color: #afbdcf;
                    top: -0.75vh;
                    left:2vh;
                    background: #ffffff;
                    padding: 0px 5px 0px 5px;
                }

                .field_name {
                    position: absolute;
                    font-size: 1em;
                    top: 1vh;
                    left: 0.5vh;
                    pointer-events: none;
                    transition:0.2s ease all;
                }

                input:focus {
                    outline:none;
                }
            }

            .type_selector {
                width: 100%;
                > * {
                    height: 4vh;
                    width: 100%;
                    background-color: rgba(0,0,0,0.2);
                    transition: all 0.2s;
                }

                *.active {
                    background-color: rgba(0,0,0,0.4);
                }
                *:hover {
                    background-color: rgba(0,0,0,0.6);
                    transform: scaleY(1.1);
                }
            }

            .smart_options {
                position: relative;
                height: 0;
                overflow-y: hidden;
                transition: all 0.6s;

                &.active {
                    margin: 1vh 0;
                    border-bottom: 2px solid #000;
                    height: auto;

                    .qm_title {
                        width: 100%;
                        position: relative;
                        z-index: 5;
                        overflow: hidden;
                        text-align: center;
                        background-color: #fff;

                        &::after,
                        &::before {
                            content: '';
                            display: inline-block;
                            width: 100%;
                            height: 2px;
                            background: #000;
                            z-index: -1;
                            position: relative;
                            vertical-align: middle;
                        }
                        &::after {
                            right: -0.2vh;
                            margin-right: -100%;
                        }
                        &::before {
                            left: -0.2vh;
                            margin-left: -100%;
                        }

                        .span {
                            pointer-events: none;
                        }

                        .info {
                            width: 2vh;
                            height: 2vh;
                            margin-left: 0.5vw;
                            background-color: gray;
                            border-radius: 50%;
                            color: white;
                        }
                    }
                }
                
                > * {
                    margin: 1vh 0;
                }
            }

            .btn_container {
                width: 100%;
            }
        }

        .content {
            position: relative;
            background-color: #363636;
            color : #fff;
            width: 20vh;
            overflow-y: hidden;

            .btn.close {
                position: absolute;
                top: 0;
                right: 0;
                width: 3vh;
                height: 3vh;
            }

            .modal_title {
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: 4vh;
            }

            .dev-item {
                margin: 2.5vh 0;
            }

            .info {
                margin-bottom: 2vh;
            }
            .btn.invite {
                background-color: rgba(0, 100, 240, 0.5);
            }

            
        }

        .notice {
            position: relative;
            margin-top: 1vh;
            width: 60vw;
            background-color: #363636;
            font-size: 1em;
            color: white;

            .options {
                div {
                    margin-top: 1vh;
                    border-bottom: 1px solid #000;
                }
            }

            &.update {
                .btn.readMore a {
                    text-decoration: none;
                    color: white;
                }
            }

            &.apply {
                background: #fff;

                input {
                    margin-top: 1vh;
                    width: 95%;
                }
            }

            &.Ntitle {
                overflow-y: hidden;
                .btn.back {
                    position: absolute;
                    background: url('../assets/back.png') no-repeat center center;
                    filter:  brightness(0) invert(1);
                    background-size: contain;
                    left: 1%;
                    width: 2.5vw;
                    height: 100%;

                    &:hover {
                        background-color: rgba(255, 255, 255, .3);
                    }
                }
            }

            .swap-state {
                .red {
                    color: rgb(0, 255, 30);
                }
                .green {
                    color: red;
                }
            }

            &.update {
                justify-content: space-between;
                .btn.readMore {
                    background:rgba(0, 240, 0, 0.5);
                    width: 50%;
                }
            }

            .btn-container {
                margin-top: 1vh;
                justify-content: space-between;
                width: 100%;
                .btn {
                    width: 100%;
                }
            }
        }
    }

    header {
        position: absolute;
        top: 0;
        width: 100%;
        flex-direction: row;
        justify-content: space-between;
        padding: 0 3vw;
        box-sizing: border-box;
        height: 6.5vh;
        background: $headerColor;
        color: white;

        .container {
            box-sizing: border-box;
            height: 4vh;
            .logo {
                box-sizing: border-box;
                height: 4vh;
                width: 4vh;
                margin-right: 1vw;
                transition: all 0.5s;

                &:hover {
                    transform: rotate(1turn);
                }
            }
        }

        .profile {
            box-sizing: border-box;
            justify-content: center;
            position: relative;
            height: 100%;
            min-width: 20vw;

            > * {
                height: 100%;
            }

            .name {
                padding: 0 2vw;
            }
            .p_btn {
                width: 2vh;
                transition: all 0.3s ease;
                filter:  brightness(0) invert(1);
                cursor: pointer;
                //margin: 0 0.5vw;
                
                &:last-child {
                    margin-right: 0;
                }

                &:hover {
                    background-color: rgba(255, 255, 255, .3);
                }
            }
            .notice_btn {
                background: url('../assets/notification.png') no-repeat center center;
                background-size: 2.5vh 2.5vh;
            }
            .logout {
                background: url('../assets/logout.png') no-repeat center center;
                background-size: 2.5vh 2.5vh;
            }
            .home {
                background: url('../assets/home.png') no-repeat center center;
                background-size: 2.5vh 2.5vh;
            }
        }
    }

    main {
        padding: 2vh 3vw;
        box-sizing: border-box;
        position: absolute;
        top : 6vh;
        width: 100%;

        .qItem, .QmakerContainer {
            position: relative;
            padding: 1vh 1vw;
            margin: 1vh 1vw;
            box-sizing: border-box;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            align-items: center;
            width: 15vw;
            height: 40vh;
            color: white;
            animation: fold 0.5s;
            animation-timing-function: ease;

            .quicklook {
                position: absolute;
                width: 100%;
                height: 100%;
                top: 0;
                left: 0;
                background-color: #484848;
                color: white;
                font-weight: 400;
                z-index: 1;
                animation: fold 0.5s;

                .quicklook__item.u {
                    color: rgba(0, 240, 0, 0.9);
                }

                .quicklook__title {
                    position: absolute;
                    top: 0.5vh;
                }

                span {
                    font-size: 1.1em;
                }
            }
        }

        .qItem, 
        .QmakerContainer {
            &::after {
                content: "";
                bottom: 0;
                position: absolute;
                width: 100%;
                height: 0;
                background: rgba(245, 191, 66, .7);
                //background: url('../assets/beer.jpg') no-repeat center center;
                background-size: cover;
                transition: height 0.5s;
                z-index: -1;
            }
        }

        .qItem:hover, 
        .QmakerContainer:hover {
            &::after {
                height: 100%;
            }
        }

        .QmakerContainer {
            display: flex;
            justify-content: center;
            align-items: center;

            .btn.about {
                background-color: rgba(10, 200, 200, 0.7);
            }
        }

        .qList {
            width: 100%;
            animation: fold 0.5s;
            animation-timing-function: ease;

            .subject-name {
                position: relative;
                height: 5vh;
                color: #fff;
                font-weight: bold;
                background-color: #E6195E;

                .back {
                    position: absolute;
                    background: url('../assets/back.png') no-repeat center;
                    filter:  brightness(0) invert(1);
                    background-size: 2.5vh 2.5vh;
                    left: 1%;
                }

                .btn_container {
                    position: absolute;
                    justify-content: space-between;
                    height: 100%;
                    top: 0;
                    right: 1%;
                    .copy {
                        background: url('../assets/copy.png') no-repeat center;
                        filter:  brightness(0) invert(1);
                        background-size: 2.5vh 2.5vh;
                    }
    
                    .exit {
                        background: url('../assets/logout.png') no-repeat center;
                        filter:  brightness(0) invert(1);
                        background-size: 2.5vh 2.5vh;
                    }
                }

                .p_btn {
                    height: 100%;
                    width: 4vh;
                    transition: all 0.2s;

                    &:hover {
                        background-color: rgba(255, 255, 255, 0.5);
                    }
                }
            }

            ul {
                margin-top: 1vh;
                width: 100%;
                height: 81vh;
                overflow-y: overlay;
                li.item {
                    position: relative;
                    width: 100%;
                    height: 4vh;
                    padding: 0 1.7vw;
                    box-sizing: border-box;
                    margin-bottom: 1vh;
                    background-color: #CE517B;
                    overflow-y: hidden;

                    > * {
                        box-sizing: border-box;
                        color: white;
                        height: 100%;
                    }

                    &.u {
                        background-color: #ee4b82;
                    }

                    .position {
                        position: absolute;
                        left: 1%;
                        width: 4vh;
                        background-color: rgba(0, 0, 0, 0.2);
                    }

                    .btn_container {
                        position: absolute;
                        top: 0;
                        right: 1%;
                        width: 8vh;
                        font-weight: bold;
                    }

                    .btn.vk {
                        text-decoration: none;
                        color: #fff;
                        width: 50%;
                    }
                }
            }
        }
    }
}

@keyframes rotation {
    0% {
        transform: rotate(5deg);
    }
    50% {
        transform: rotate(-5deg);
    }
    100%{
        transform: rotate(0deg);
    }
}

@keyframes fold {
    from {
      opacity: 0;
    }
    to {
      opacity: 1;
    }
}

@keyframes loading {
    0% {
        transform: rotate(180deg);
    }
    50% {
        transform: rotate(-360deg) scale(0.6);
    }
    100% {
        transform: rotate(0deg)
    }
}
